'use client'

import {useEffect, useRef, useState} from 'react'
import 'jsoneditor/dist/jsoneditor.min.css'
import JSONEditor from 'jsoneditor'
import {EMPTY_OBJECT} from '@nyx/kr-common/constant/Constant'

/**
 * https://github.com/josdejong/jsoneditor
 * https://codesandbox.io/p/sandbox/react-json-editor-osveq?file=%2Fsrc%2FApp.js%3A9%2C22
 */
export default function JsonEditor() {
    const codeEditorRef = useRef<HTMLDivElement>(null)
    const viewEditorRef = useRef<HTMLDivElement>(null)
    const codeEditorInstance = useRef<JSONEditor | null>(null)
    const viewEditorInstance = useRef<JSONEditor | null>(null)
    const [jsonData, setJsonData] = useState(EMPTY_OBJECT)

    useEffect(() => {
        codeEditorInstance.current = new JSONEditor(codeEditorRef.current!, {
            mode: 'code',
            onChange: () => {
                setJsonData(codeEditorInstance.current!.get())
            }
        })

        viewEditorInstance.current = new JSONEditor(viewEditorRef.current!, {
            mode: 'tree'
        })

        // destructor
        return () => {
            if (codeEditorInstance.current) {
                codeEditorInstance.current.destroy()
            }
            if (viewEditorInstance.current) {
                viewEditorInstance.current.destroy()
            }
        }
    }, [])

    useEffect(() => {
        if (viewEditorInstance.current) {
            viewEditorInstance.current.set(jsonData)
        }
    }, [jsonData])

    return (
        <div style={{display: 'flex', height: '100vh', gap: '10px', padding: '10px'}}>
            <div ref={codeEditorRef} style={{flex: 1, height: '100%'}}></div>
            <div ref={viewEditorRef} style={{flex: 1, height: '100%'}}></div>
        </div>
    )
}